@charset "UTF-8";

//NHA-COMMON-RMA-025
.support_rma {
  .rma_list {
    padding:20px 0 40px; max-width:1200px; margin:0 auto;
    ol {
      @extend %clearfix;
    }
    li {
      width:calc(50% - 10px); margin:20px 0 0; float:left; border:1px solid $gray2; overflow:hidden;
      @include borderRadius(10px);
      @include boxSizing;
      &:nth-of-type(2n) {float:right;}
      &:nth-of-type(2n+1) {clear:both;}
    }
    .img_wrap {
      position:relative; overflow:hidden;
      button {
        width:76px; height:76px; position:absolute; top:50%; left:50%; background-image:url(../../images/support/play_btn.png); z-index:1; text-indent:-9999px;
        @include translate(-50%, -50%);
        @include backgroundSoltC;
        &:hover,
        &:focus {
          & ~ img {@include scale(1.15);}
        }
      }
      img {
        width:100%;
        @include transition;
      }
    }
    .txt_wrap {
      padding:40px 50px; min-height:209px;
      a {
        font-size:26px; font-weight:$bold; color:$gray7;
        @include transition(color, .2s);
        &:hover,
        &:focus {color:$blue;}
      }
      .txt01 {font-size:16px; line-height:25px; color:$gray7; margin:20px 0 0;}
      .txt02 {
        font-size:14px; line-height:22px; color:$gray5; margin:12px 0 0; padding:12px 0 0; border-top:1px solid #e3e3e3;
      }
    }
  }

  @include mob() {
    .rma_list {
      padding:em(40) em(30);
      li {
        width:100%; margin:em(20) 0 0;
        @include borderRadius(1rem);
        &:first-child {margin:0;}
      }
      .img_wrap {
        button {width:76px; height:76px; background-image:url(../../images/support/play_btn.png);}
      }
      .txt_wrap {
        padding:em(60) em(55); min-height:auto;
        a {font-size:em(52);}
        .txt01 {font-size:em(32); line-height:em(42); margin:em(30) 0 0;}
        .txt02 {font-size:em(26); line-height:em(34); margin:em(30) 0 0; padding:em(30) 0 0;}
      }
    }
  }
}